.taskContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 14px;
  margin-top: 27px;
}
.taskElement {
  display: flex;
  flex-direction: column;
  padding: 16px;
  border-radius: 12px;
  border: 2px solid #09bdc6;
}
.taskTitle {
  font-size: 28px;
  font-weight: 600;
  color: #000;
  margin-top: 0px;
  margin-bottom: 6px;
}
.taskText {
  font-size: 16px;
  font-weight: 400;
  color: #000;
}
